import * as helperStorage from '../common/util/storage';

const defaultRoute = '/get-involved';

const convertDataToNodes = (data) => {
    const container = document.createElement('div');
    container.innerHTML = data;
    
    return container;
};

const saveFormInStorage = (nodesContainer) => {
    const transactionId = nodesContainer.querySelector('meta[name="tid"]')?.content;

    const currentForm = helperStorage.getFromStorage(helperStorage.GIVE_FORM) || {};
    
    const newForm = {
        ...currentForm,
        transactionId
    };

    helperStorage.saveToStorage(helperStorage.GIVE_FORM, newForm);
};

const manageStorage = ( errorText, mustRedirectTo ) => {
    helperStorage.deleteFromStorage(helperStorage.PAYMENT_ERROR);
    helperStorage.saveToStorage(helperStorage.PAYMENT_ERROR, { errorText, mustRedirectTo });
}

const saveErrorInStorage = (nodesContainer, mustRedirectTo) => {
    const errorText = nodesContainer.querySelector('meta[name="payment_error"]')?.content;
    manageStorage( errorText, mustRedirectTo);
};

const savePayPalErrorInStorage = (mustRedirectTo) => {
    const errorText = "An error occurred during the transaction, please try again."
    manageStorage( errorText, mustRedirectTo);
};

const responseTypes = {
    success: {
        route: 'success',
        action: saveFormInStorage
    },
    decline: {
        route: 'error',
        action: saveErrorInStorage
    },
    pending: {
        route: 'pending',
    }
};

const getPaymentResponseType = (nodesContainer) => {
    const metaTagType = nodesContainer.querySelector('meta[name="ttype"]')?.content;
    
    return responseTypes[metaTagType] || responseTypes.success;
};

export const redirectByResponse = ({ data }, mustRedirectTo = `${{defaultRoute}}/give` ) => {
    const nodesContainer = convertDataToNodes(data);
    const responseType = getPaymentResponseType(nodesContainer);
    
    responseType?.action?.( nodesContainer, mustRedirectTo );
    
    window.location = `${defaultRoute}/${responseType.route}`;
};

export const redirectByResponsePayPal = (isError, mustRedirectTo = `${{defaultRoute}}/give`) => {
    if ( isError ) savePayPalErrorInStorage( mustRedirectTo );
    
    window.location = isError
        ? `${defaultRoute}/error`
        : `${defaultRoute}/success`;
}

export const formatMoney = (amount, decimalCount = 2, decimal = ".", thousands = ",") => {
    try {
        decimalCount = Math.abs(decimalCount);
        decimalCount = isNaN(decimalCount) ? 2 : decimalCount;

        const negativeSign = amount < 0 ? "-" : "";

        let i = parseInt(amount = Math.abs(Number(amount) || 0).toFixed(decimalCount)).toString();
        let j = (i.length > 3) ? i.length % 3 : 0;

        return negativeSign + (j ? i.substr(0, j) + thousands : '') + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + thousands) + (decimalCount ? decimal + Math.abs(amount - i).toFixed(decimalCount).slice(2) : "");
    } catch (e) {
        console.error(e)
    }
};

